// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Col,
  Glyphicon,
  Image,
} from 'react-bootstrap';

// custom
import {
  skillImgs,
  namekoSkills,
  namekoImgs,
} from '../lib/data';

export default class extends Component {
  render() {
    const { nameko, isSelected, editable, onClick, onClickEdit } = this.props;
    const { name, skills, values } = nameko;

    const className = 'nameko' + (isSelected ? ' selected' : '') + (onClick ? ' clickable' : '');

    return (
      <Col className='nameko-wrapper' xs={4} sm={2}>
        <div className={className}>
          <div
            className='top-ctn'
            onClick={() => onClick && onClick(nameko)}
          >
            <div className='img-ctn'>
              <Image
                className='img'
                src={namekoImgs[name]}
              />
              <div className='name'>{name}</div>
            </div>
            <div className='skill-ctn'>
              {namekoSkills[name].map((skill, idx) => (
                <div
                  key={idx}
                  style={{ backgroundImage: `url(${skillImgs[skill]})` }}
                  className={'skill' + (skills.indexOf(skill) === -1 ? '' : ' selected')}
                />
              ))}
            </div>
          </div>
          <div className='value-ctn'>
            {values.map((value, idx) => (
              <span
                key={idx}
                style={{ backgroundImage: `url(./imgs/${['red', 'blue', 'green'][idx]}.png` }}
              >
                {value}
              </span>
            ))}
          </div>
          {editable && onClickEdit && <div>
            <Button onClick={() => onClickEdit(nameko)}>
              <Glyphicon glyph='pencil' />
              編輯
            </Button>
          </div>}
        </div>
      </Col>
    );
  }
}
