// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Clearfix,
  Modal,
} from 'react-bootstrap';

// custom
import NamekoEditor from './NamekoEditor';
import Nameko from './Nameko';

export default class extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mode: 'list',
      nameko: null,
    };
  }

  showEditor(nameko) {
    this.setState({
      mode: nameko ? 'editor' : 'list',
      nameko: nameko || null,
    });
  }

  render() {
    const { mode, nameko } = this.state;
    const {
      show, showModal, selectedLevel, levels, selectedNamekos,
      onClickLevel, onResetCustom, onClickAction, onSelectNameko, onSelectNamekos,
    } = this.props;

    return (
      <Modal
        className='nameko-modal n-test'
        show={show}
        onHide={() => {
          showModal(false);
          this.showEditor(false);
        }}
      >
        <Modal.Header>
          {mode !== 'editor' && <div style={{ float: 'right' }}>
            <Button onClick={() => onSelectNamekos(selectedLevel.namekos)}>全選</Button>
            <Button onClick={() => onSelectNamekos([])}>全不選</Button>
            {levels.indexOf(selectedLevel) === 0 && <Button onClick={() => onResetCustom()}>重設</Button>}
          </div>}
          <Modal.Title>選擇菇菇</Modal.Title>
        </Modal.Header>
        <Modal.Body
          onWheel={e => mode === 'editor' && e.nativeEvent.preventDefault()}
        >
          <div className='list'>
            <div>
              {levels.map((level, idx) => (
                <Button
                  key={idx}
                  onClick={() => onClickLevel(level)}
                  style={{ fontWeight: selectedLevel === level ? 'bold' : 'normal' }}
                >
                  {level.name}
                </Button>
              ))}
            </div>
            <div className='all-block'>
              {(() => {
                const comps = [];

                selectedLevel.namekos.forEach((nameko, idx) => {
                  comps.push(<Nameko
                    key={idx}
                    nameko={nameko}
                    isSelected={selectedNamekos.includes(nameko)}
                    editable={!!selectedLevel.editable}
                    onClick={() => onSelectNameko(nameko)}
                    onClickEdit={() => this.showEditor(nameko)}
                  />);

                  if (idx % 3 === 2) {
                    if (idx % 6 === 5) {
                      comps.push(<Clearfix key={`_${idx}`} visibleXsBlock visibleSmBlock />);
                    } else {
                      comps.push(<Clearfix key={`_${idx}`} visibleXsBlock />);
                    }
                  }
                });

                return comps;
              })()}
            </div>
          </div>
          <NamekoEditor
            nameko={nameko}
            show={mode === 'editor'}
            onClick={onClickAction}
          />
        </Modal.Body>
        <Modal.Footer>
          <Button
            bsSize='large'
            bsStyle='primary'
            onClick={() => mode === 'list' ? showModal(false) : this.showEditor(false)}
          >
            完成
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
