// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Panel,
} from 'react-bootstrap';

export default class extends Component {
  render() {
    const { tunnel, results } = this.props;

    const comps = [];

    results.slice(0, 1000).forEach((r, idx) => {
      const { team, passedMemos, unpassedMemos } = r;

      const r2 = results[idx - 1];

      if (!r2 || r.passedMemos.length !== r2.passedMemos.length) {
        comps.push({
          header: <h2 key={idx}>{`${passedMemos.length}/${tunnel.memos.length}`}</h2>,
          items: [],
        });
      }

      comps[comps.length - 1].items.push(
        <div key={idx} style={{ marginBottom: '5px' }}>
          <div>
            {team.map(n => n.name).join(', ')}
          </div>
          <div>
            {unpassedMemos.map((memo, idx) => {
              const { type, reward: r, valueThresholds: v, skills: s, name } = memo;

              return (
                <div key={idx}>
                  {'(X) '}
                  <span style={{ color: memo.rewardType==='item'?'red':'blue'}}>
                    {memo.rewardType==='item'?'道具':'寶藏'}
                  </span>
                  {` ${r[0]}*${r[1]} ${type}${v?v.map(a=>a.join(':')).join() : ''}${s?s.join(','):''} ${name}`}
                </div>
              );
            })}
          </div>
        </div>
      );
    });

    return (
      <Panel className='result-panel'>
        {!!results.length && <h1>{`${tunnel.group}(${tunnel.name})`}</h1>}
        {comps.map((comp, idx) => (
          <div key={idx}>
            {comp.header}
            {comp.items}
          </div>
        ))}
      </Panel>
    );
  }
}
