// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Clearfix,
  Dropdown,
  Glyphicon,
  MenuItem,
  Modal,
  Panel,
} from 'react-bootstrap';

// 3rd-party
import Cookies from 'universal-cookie';

// custom
import './style.css';
import { allSkills, allLevels, allTunnels, allNamekos, namekoIds, skillIds, sortConds } from './lib/data';
import { goAll } from './lib/test';
import TunnelModal, { TunnelBanner } from './comps/TunnelModal';
import Nameko from './comps/Nameko';
import NamekoModal from './comps/NamekoModal';
import ResultPanel from './comps/ResultPanel';

const cookies = new Cookies();

const copyCustomLevel = () => ({
  ...JSON.parse(JSON.stringify(allLevels[allLevels.length - 1])),
  name: '自訂',
  editable: true,
});

class App extends Component {
  constructor(props) {
    super(props);

    const levels = allLevels.slice();
    const selectedLevel = copyCustomLevel();
    levels.splice(0, 0, selectedLevel);

    const selectedNamekos = selectedLevel.namekos.slice(0, 7);
    //const selectedNamekos = selectedLevel.namekos.slice();

    this.load(selectedLevel, selectedNamekos);

    this.state = {
      forceUpdate: {},
      tunnel: allTunnels[2],
      selectedLevel,
      levels,
      selectedNamekos,
      sortCond: sortConds[0],
      results: [],
      tOpened: false,
      nOpened: false,
      lOpened: false,
      percentage: 0,
    };

    this.useWorker = !!window.Worker;
    //this.useWorker = false;
  }

  componentDidMount() {
    //document.body.style.backgroundImage = "url('./imgs/bg.jpg')";
    document.getElementById('bg').style.backgroundImage = "url('./imgs/bg.jpg')";

    if (this.useWorker) {
      this.worker = new Worker('worker.js');
      this.worker.onmessage = e => {
        const { type, results } = e.data;
        if (type === 'percentage') {
          this.setState({ percentage: results });
        } else if (type === 'results') {
          console.log('DONE!', `end: ${new Date()}`);
          this.setState({ percentage: 0, results, lOpened: false });
        } else {
          console.log('ERROR!', `end: ${new Date()}`, results); // TODO handle errors
          this.setState({ percentage: 0, results: [], lOpened: false });
        }
      };
    }
  }

  updateUI() {
    this.setState({ forceUpdate: {} }, () => this.save());
  }

  load(level, namekos) {
    let data = cookies.get('custom');
    if (!data) {
      console.log('??? SKIP load');
      return;
    }

    console.log('??? DO load');

    const loadeds = {};

    data.names.forEach((nameId, i) => {
      const name = allNamekos[nameId];
      const skills = data.skills[i].map(skillId => allSkills[skillId]);
      const values = data.values[i];
      loadeds[name] = { name, skills, values };
    });

    level.namekos.forEach(n => {
      if (loadeds[n.name]) {
        n.skills = loadeds[n.name].skills;
        n.values = loadeds[n.name].values;
        loadeds[n.name].nameko = n;
      }
    });

    namekos.splice(0, namekos.length);

    data.selected.forEach(nameId => {
      const name = allNamekos[nameId];
      namekos.push(loadeds[name].nameko);
    });
  }

  save() {
    const { selectedLevel, levels, selectedNamekos } = this.state;

    if (levels.indexOf(selectedLevel) !== 0) {
      console.log('!!! SKIP save');
      return;
    }

    console.log('!!! DO save');
    const { namekos } = selectedLevel;
    const data = {};
    data.selected = selectedNamekos.map(n => namekoIds[n.name]);
    data.names = namekos.map(n => namekoIds[n.name]);
    data.skills = namekos.map(n => n.skills.map(s => skillIds[s]));
    data.values = namekos.map(n => n.values);
    cookies.set('custom', data, { path: '/' });
  }

  onSelectTunnel(tunnel) {
    this.setState({ tunnel });
  }

  onSelectNameko(nameko) {
    const { selectedNamekos } = this.state;
    const idx = selectedNamekos.indexOf(nameko);

    if (idx === -1) {
      selectedNamekos.push(nameko);
      selectedNamekos.sort((a, b) => allNamekos.indexOf(a.name) - allNamekos.indexOf(b.name));
    } else {
      selectedNamekos.splice(idx, 1);
    }

    this.updateUI();
  }

  onSelectNamekos(namekos) {
    const { selectedNamekos } = this.state;

    selectedNamekos.splice(0, selectedNamekos.length);
    Array.prototype.push.apply(selectedNamekos, namekos);

    this.updateUI();
  }

  onClickNamekoAction(nameko, type, value) {
    if (type === 'skill') {
      const { skills } = nameko;
      const idx = skills.indexOf(value);
      if (idx === -1) {
        skills.push(value);
      } else {
        skills.splice(idx, 1);
      }
    } else if (type === 'sub_value') {
      if (nameko.values[value] <= 5) {
        return;
      }
      nameko.values[value] -= 5;
    } else if (type === 'add_value') {
      nameko.values[value] += 5;
    }
    this.updateUI();
  }

  onClickLevel(level) {
    this.setState({
      selectedLevel: level,
      selectedNamekos: level.namekos.slice(),
    });
  }

  onResetCustom() {
    Object.assign(this.state.levels[0], copyCustomLevel());
    this.setState({ selectedNamekos: this.state.levels[0].namekos.slice() });
    this.updateUI();
  }

  onClickRun() {
    console.log(' >> onClickRun', `start: ${new Date()}`, `worker: ${this.useWorker}`);

    this.setState(
      { lOpened: true },
      () => {
        const { tunnel, selectedNamekos: namekos, sortCond } = this.state;

        if (this.useWorker) {
          this.worker.postMessage({ tunnel, namekos, sortCond });
        } else {
          const results = goAll(tunnel, namekos);
          console.log('DONE!', `end: ${new Date()}`);
          this.setState({ results, lOpened: false });
        }
      },
    );

  }

  showTModal(tOpened) {
    this.setState({ tOpened });
  }

  showNModal(nOpened) {
    this.setState({ nOpened });
  }

  render() {
    const {
      tunnel, selectedLevel, levels,
      selectedNamekos, sortCond, results,
      tOpened, nOpened, lOpened,
    } = this.state;

    return (
      <div className='app'>
        <div id='bg'></div>
        <div className='banner-placeholder'></div>
        <TunnelBanner
          text={`${tunnel.group}(${tunnel.name})`}
          bgKey={tunnel.group}
          btnCfg={{
            text: '選擇洞穴',
            onClick: () => this.showTModal(true),
          }}
        />
        <Panel className='main'>
          <Panel.Heading>
            {`選擇了 ${selectedNamekos.length} 隻菇菇`}
            <Button onClick={() => this.showNModal(true)}>選擇菇菇</Button>
          </Panel.Heading>
          <Panel.Body>
            {(() => {
              const comps = [];

              selectedNamekos.forEach((nameko, idx) => {
                comps.push(<Nameko
                  key={idx}
                  nameko={nameko}
                />);

                if (idx % 3 === 2) {
                  comps.push(<Clearfix key={`_${idx}`} visibleXsBlock />);
                }
              });

              return comps;
            })()}
          </Panel.Body>
        </Panel>
        <ResultPanel
          tunnel={tunnel}
          results={results}
        />
        <div className='footer-placeholder'></div>
        <div
          className='banner'
          style={{ backgroundImage: `url('./imgs/banner.png')` }}
        ></div>
        <div
          className='footer dropup'
        >
          <Dropdown
            id='sort'
            bsSize='large'
          >
            <Dropdown.Toggle>
              {'結果排序: ' + sortCond}
              <Glyphicon glyph='triangle-top' />
            </Dropdown.Toggle>
            <Dropdown.Menu>
              {sortConds.map((c, idx) => <MenuItem
                key={idx}
                onClick={() => this.setState({ sortCond: sortConds[idx] })}
                active={sortConds[idx] === sortCond}
              >
                {sortConds[idx]}
              </MenuItem>)}
            </Dropdown.Menu>
          </Dropdown>
          <Button
            id='run'
            bsSize='large'
            bsStyle='success'
            onClick={() => this.onClickRun()}
          >
            <Glyphicon glyph='play' />
            執行測試
          </Button>
        </div>
        <TunnelModal
          show={tOpened}
          showModal={val => this.showTModal(val)}
          selectedTunnel={tunnel}
          onSelectTunnel={tunnel => this.onSelectTunnel(tunnel)}
        />
        <NamekoModal
          show={nOpened}
          showModal={val => this.showNModal(val)}
          selectedLevel={selectedLevel}
          levels={levels}
          selectedNamekos={selectedNamekos}
          onClickLevel={level => this.onClickLevel(level)}
          onResetCustom={this.onResetCustom.bind(this)}
          onSelectNameko={this.onSelectNameko.bind(this)}
          onSelectNamekos={this.onSelectNamekos.bind(this)}
          onClickAction={this.onClickNamekoAction.bind(this)}
        />
        <Modal
          animation={false}
          show={lOpened}
        >
          <Modal.Body>
            loading ...
            {this.useWorker && ` ${this.state.percentage}%`}
          </Modal.Body>
        </Modal>
      </div>
    );
  }
}

export default App;
