import {
  allTreasures,
  allItems,
  allSkills,
  allNamekos,
  tunnelScopes,
} from './data';

function go(tunnel, team) {
  const log = () => null;
  //const log = console.log;

  log(' >> Tunnel', tunnel.key);
  log('          ', team.map(n => n.name).join(', '));

  let passedMemos = [];
  let unpassedMemos = [];

  for (let memo of tunnel.memos) {
    const { type, valueThresholds: vts, skillValueBonus: svb, skills } = memo;
    const [ skill, sValue, bonus, needAll ] = (svb || []);
    log('      Memo');
    skills && log('        type', type, `[${skills.join(', ')}]`);
    vts    && log('        type', type, `[${(vts || []).map(a => a.join(':')).join(', ')}]`);
    svb    && log('        svb', svb.join(' '));

    let passed = false;

    if ('總和' === type) {
      const [ value, threshold ] = vts[0]; // TODO 會有多個技能組的可能嗎？

      let namekoValue = 0;
      let allHas = true;

      for (let member of team) {
        const nameko = {
          name:   member.name,
          skills: member.skills,
          red:    member.values[0],
          blue:   member.values[1],
          green:  member.values[2],
        };

        if (needAll && !nameko.skills.includes(skill)) {
          allHas = false;
        }

        namekoValue += nameko[value];

        if (!needAll && nameko.skills.includes(skill) && value === sValue) {
          namekoValue += bonus;
        }
      }

      if (skill && needAll && allHas) {
        namekoValue += bonus;
      }

      if (namekoValue >= threshold) {
        passed = true;
      }
    } else if ('有1隻' === type || '有2隻' === type || '有3隻' === type) {
      const needCount = parseInt(type.substr(1, 1));
      let currentCount = 0;

      for (let member of team) {
        let res = true;

        const nameko = {
          name:   member.name,
          skills: member.skills,
          red:    member.values[0],
          blue:   member.values[1],
          green:  member.values[2],
        };

        for (let vt of vts) {
          const [ value, threshold ] = vt;

          let namekoValue = nameko[value];
          if (nameko.skills.includes(skill) && value === sValue) {
            namekoValue += bonus;
          }

          if (namekoValue < threshold) {
            res = false;
            break;
          }
        }

        log(`          (${res ? 'O' : 'X'})`, nameko.name, `[${nameko.skills.join(', ')}]`, nameko.red, nameko.blue, nameko.green);

        if (res) {
          currentCount++;
          if (currentCount === needCount) {
            passed = true;
            break;
          }
        }
      }
    } else if ('1隻有skill' === type) {
      for (let nameko of team) {
        let res = true;

        for (let s of skills) {
          if (!nameko.skills.includes(s)) {
            res = false;
            break;
          }
        }

        if (res) {
          passed = true;
          break;
        }
      }
    } else if ('全部有skill' === type) {
      let count = 0;

      for (let nameko of team) {
        let res = true;

        for (let s of skills) {
          if (!nameko.skills.includes(s)) {
            res = false;
            break;
          }
        }

        if (res) {
          count++;
        }
      }

      passed = count === team.length;
    }

    (passed ? passedMemos : unpassedMemos).push(memo);
    log(`        passed => (${passed ? 'O' : 'X'})`);
  }

  log('    Memos', passedMemos);
  log(`    pass ${passedMemos.length} of ${tunnel.memos.length}`);
  log(' >> end tunnel', tunnel.key);

  return { team, passedMemos, unpassedMemos };
}

export function goAll(tunnel, namekos, noDup = true, sortCond = 'byPassCount', percentageFunc = () => null) {
  console.log(' ** goAll', tunnel.key + ' with all combinations!');
  console.log('    noDup', noDup, 'sortCond', sortCond);
  if (!verifyTunnel(tunnel)) {
    throw 'goAll: BAD tunnel';
  }

  if (!verifyNamekos(namekos)) {
    throw 'goAll: BAD namekos';
  }

  if (tunnelScopes[tunnel.key]) {
    namekos = namekos.filter(n => tunnelScopes[tunnel.key].includes(n.name));
  }

  const count = tunnel.count || 3;

  //noDup = false;
  if ((!noDup && namekos.length <= 0) || (noDup && count > namekos.length)) {
    console.log(noDup, count, namekos.length);
    throw 'goAll: BAD count';
  }

  let total = namekos.length + (noDup ? 0 : 2);
  total = total * (total - 1) * (total - 2) / 6;
  //console.log('total', total);

  // pick all combinations
  const results = [];

  (function pickTeam(prevTeam = [], idx = 0, top = true) {
    for (let i = idx; i < namekos.length; i++) {
      const team = prevTeam.slice();
      team.push(namekos[i]);

      if (team.length === count) {
        // test each combination
        results.push(go(tunnel, team));
      } else {
        pickTeam(team, i + (noDup ? 1 : 0), false);
      }

      if (top) {
        percentageFunc(Math.round(100 * results.length / total));
      }
    }
  })();

  // sort results
  sortResults(results, sortCond);

  // show results
  printResults(results, tunnel);

  return results;
}

function verifyTunnel(tunnel) {
  // TODO
  return true;
}

function verifyNamekos(namekos) {
  for (let n of namekos) {
    if (!allNamekos.includes(n.name)) {
      console.log(' >> verify bad 1', n, namekos);
      return false;
    }
    for (let v of n.values) {
      if (!Number.isInteger(v) || v <= 0) {
        console.log(' >> verify bad 2', n, namekos);
        return false;
      }
    }
    for (let s of n.skills) {
      if (!allSkills.includes(s)) {
        console.log(' >> verify bad 3', n, namekos);
        return false;
      }
    }
  }
  return true;
}

function sortResults(results, sortCond) {
  results.forEach(r => {
    let treature = 0,
      treatureMemo = 0,
      item = 0,
      itemMemo = 0;

    r.passedMemos.forEach(m => {
      if (m.rewardType === 'treature') {
        treature += m.reward[1];
        treatureMemo++;
      } else {
        item += m.reward[1];
        itemMemo++;
      }
    });

    r.count = { treature, treatureMemo, item, itemMemo };
    //console.log('  count', r.count);
  });

  const byPassCount = (r1, r2) => r2.passedMemos.length - r1.passedMemos.length,
    byT = (r1, r2) => r2.count.treature - r1.count.treature,
    byTMemo = (r1, r2) => r2.count.treatureMemo - r1.count.treatureMemo,
    byI = (r1, r2) => r2.count.item - r1.count.item,
    byIMemo = (r1, r2) => r2.count.itemMemo - r1.count.itemMemo;

  let funcs = [byPassCount, byIMemo, byI, byTMemo, byT];
  if (sortCond === 'byTreasure') {
    funcs = [byTMemo, byPassCount, byT, byIMemo, byI];
  } else if (sortCond === 'byItem') {
    funcs = [byIMemo, byPassCount, byI, byTMemo, byT];
  }

  results.sort((r1, r2) => {
    for (let i = 0; i < funcs.length; i++) {
      let r = funcs[i](r1, r2);
      if (r !== 0 || i === funcs.length - 1) {
        //console.log('return r', r);
        return r;
      }
    }
    console.log('return WTF');
  });
}

function printResults(results, tunnel) {
  const limit = Math.min(50, results.length);
  for (let i = limit - 1; i >= 0; i--) {
    printResult(results[i], tunnel);
  }
}

function printResult(result, tunnel) {
  const log = () => null;
  //const log = console.log;

  //const showPassed = true;
  const showPassed = false;
  const showUnpassed = true;
  //const showUnpassed = false;

  let { team, passedMemos, unpassedMemos } = result;

  log(`    ${passedMemos.length}/${tunnel.memos.length},\t${team.map(n => n.name).join(', ')}`);

  if (showPassed) {
    for (let memo of passedMemos) {
      let { type, reward: r, valueThresholds: v, skills: s, name } = memo;
      log(`      ${showUnpassed?'(O)':''}${r[0]}*${r[1]}  \t${type}${v?v.map(a=>a.join(':')).join() : ''}${s?s.join(','):''}\t${name}`);
    }
  }

  if (showUnpassed) {
    for (let memo of unpassedMemos) {
      let { type, reward: r, valueThresholds: v, skills: s, name } = memo;
      log(`      ${showPassed?'(X)':''}${r[0]}*${r[1]}  \t${type}${v?v.map(a=>a.join(':')).join() : ''}${s?s.join(','):''}\t${name}`);
    }
  }
}
