// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Clearfix,
  Col,
  Glyphicon,
  Label,
  Modal,
  Panel,
} from 'react-bootstrap';

// custom
import {
  allTunnels,
  tunnelBgs,
} from '../lib/data';

class Memo extends Component {
  render() {
    const { memo } = this.props;
    const { type, valueThresholds: vt, skillValueBonus: vb, skills: s, name, reward } = memo;

    return (
      <Col
        className='memo'
        xs={6}
      >
        <div>{name}</div>
        <div>
          <span>{type}</span>
          {vt && <span>{vt.map(a => a.join(':')).join(',')}</span>}
          {vb && <span>{`${vb[0]} ${vb[1]}的 +${vb[2]}`}</span>}
          {s && <span>{`某隻擁有 ${s}`}</span>}
        </div>
        <div>
          <span style={{ color: memo.rewardType === 'item' ? 'red' : 'blue'}}>
            {memo.rewardType === 'item' ? '道具' : '寶藏'}
          </span>
          {` ${reward[0]} * ${reward[1]}`}
        </div>
      </Col>
    );
  }
}

export class Tunnel extends Component {
  constructor(props) {
    super(props);
    this.state = { expanded: false };
  }

  render() {
    const { expanded } = this.state;
    const { tunnel, isSelected, onClick } = this.props;
    const { name, count, memos } = tunnel;

    return (
      <Panel
        className='tunnel'
        expanded={expanded}
        onToggle={() => null}
      >
        <Panel.Heading
          onClick={() => this.setState({ expanded: !expanded })}
        >
          <Label
            className='check'
            onClick={e => {
              e.stopPropagation();
              !isSelected && onClick(tunnel);
            }}
          >
            {isSelected ? <Glyphicon glyph='ok' /> : ' '}
          </Label>
          <Label>{name}</Label>
          <Button active={expanded}>
            <Glyphicon glyph='list' />
          </Button>
        </Panel.Heading>
        <Panel.Collapse>
          <Panel.Body>
            <div>
              <div>{'菇菇數量: ' + (count || 3)}</div>
              <div>
                {(() => {
                  const comps = [];

                  memos.forEach((memo, idx) => {
                    comps.push(<Memo key={idx} memo={memo} />);

                    if (idx % 2 === 1) {
                      comps.push(<Clearfix key={`_${idx}`} visibleXsBlock />);
                    }
                  });

                  return comps;
                })()}
              </div>
            </div>
          </Panel.Body>
        </Panel.Collapse>
      </Panel>
    );
  }
}

export class TunnelBanner extends Component {
  constructor(props) {
    super(props);
    this.state = { expanded: !!props.isSelected };
  }

  render() {
    const { expanded } = this.state;
    const { children, text, bgKey, isSelected, btnCfg } = this.props;
    //console.log('banner props', this.props);

    const selectable = typeof isSelected === 'boolean';
    const className = 'tunnel-banner' + (selectable ? ' selectable' : '');

    return (
      <Panel
        className={className}
        expanded={expanded}
        onToggle={() => null}
      >
        <Panel.Heading
          style={{ backgroundImage: `url(${tunnelBgs[bgKey]})` }}
          onClick={() => selectable && this.setState({ expanded: !expanded })}
        >
          {selectable && <Label className='check'>
            {isSelected ? <Glyphicon glyph='ok' /> : ' '}
          </Label>}
          <Label>{text}</Label>
          {btnCfg && <Button onClick={() => btnCfg.onClick()}>{btnCfg.text}</Button>}
          {children && <Button onClick={() => this.setState({ expanded: !expanded })}>
            <Glyphicon glyph={`menu-${expanded ? 'up' : 'down'}`} />
          </Button>}
        </Panel.Heading>
        {children && <Panel.Collapse>
          <Panel.Body>
            {children}
          </Panel.Body>
        </Panel.Collapse>}
      </Panel>
    );
  }
}

class TunnelGroup extends Component {
  render() {
    const { group, clickedTunnel, onClick } = this.props;
    const { groupName, tunnels } = group;

    return (
      <div>
        <TunnelBanner
          text={groupName}
          bgKey={groupName}
          isSelected={tunnels.includes(clickedTunnel)}
        >
          {tunnels.map((tunnel, idx) => (
            <Tunnel
              key={idx}
              tunnel={tunnel}
              isSelected={tunnel === clickedTunnel}
              onClick={onClick}
            />
          ))}
        </TunnelBanner>
      </div>
    );
  }
}

export default class extends Component {
  constructor(props) {
    super(props);
    this.state = { clickedTunnel: props.selectedTunnel };
  }

  clickTunnel(clickedTunnel) {
    this.setState({ clickedTunnel });
  }

  render() {
    const { clickedTunnel } = this.state;
    const { show, showModal, selectedTunnel, onSelectTunnel } = this.props;

    const groups = [];
    allTunnels.forEach(t => {
      let group = groups[groups.length - 1];
      if (!group || group.groupName !== t.group) {
        group = {
          groupName: t.group,
          tunnels: [],
        };

        groups.push(group);
      }

      group.tunnels.push(t);
    });

    return (
      <Modal
        show={show}
        onHide={() => showModal(false)}
        onEnter={() => this.setState({ clickedTunnel: selectedTunnel })}
        onExited={() => this.setState({ clickedTunnel: selectedTunnel })}
      >
        <Modal.Header>
          <Modal.Title>{`目前選擇: ${selectedTunnel.group}(${selectedTunnel.name})`}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {groups.map((group, idx) => (
            <TunnelGroup
              key={idx}
              group={group}
              clickedTunnel={clickedTunnel}
              onClick={this.clickTunnel.bind(this)}
            />
          ))}
        </Modal.Body>
        <Modal.Footer>
          <Button
            bsSize='large'
            bsStyle='primary'
            onClick={() => {
              showModal(false);
              if (clickedTunnel !== selectedTunnel) {
                onSelectTunnel(clickedTunnel);
              }
            }}
          >
            完成
          </Button>
        </Modal.Footer>
      </Modal>
    )
  }
}
