// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Glyphicon,
  Image,
} from 'react-bootstrap';

// custom
import {
  skillImgs,
  namekoSkills,
  namekoImgs,
} from '../lib/data';

class SkillArea extends Component {
  render() {
    const { name, skills, onClick } = this.props;

    return (
      <div>
        {namekoSkills[name].map((skill, idx) => (
          <Button
            key={idx}
            className={'skill' + (skills.indexOf(skill) === -1 ? '' : ' selected')}
            style={{ backgroundImage: `url(${skillImgs[skill]})` }}
            onClick={() => onClick('skill', skill)}
          />
        ))}
      </div>
    );
  }
}

class ValueArea extends Component {
  render() {
    const { values, onClick } = this.props;

    return (
      <div
        className='value-area'
      >
        {values.map((value, idx) => (
          <div
            key={idx}
          >
            <Button
              bsSize='large'
              onClick={() => onClick('add_value', idx)}
            >
              <Glyphicon glyph='triangle-top' />
            </Button>
            <span>{value}</span>
            <Button
              bsSize='large'
              onClick={() => onClick('sub_value', idx)}
            >
              <Glyphicon glyph='triangle-bottom' />
            </Button>
          </div>
        ))}
      </div>
    );
  }
}

export default class NamekoEditor extends Component {
  render() {
    const { nameko, show, onClick } = this.props;

    if (!nameko) {
      return null;
    }

    const { name, skills, values } = nameko;

    return (
      <div
        className='editor'
        style={{ display: show ? 'block' : 'none' }}
      >
        <Image
          className='img'
          src={namekoImgs[name]}
        />
        <div>{name}</div>
        <SkillArea
          name={name}
          skills={skills}
          onClick={(type, value) => onClick(nameko, type, value)}
        />
        <ValueArea
          values={values}
          onClick={(type, value) => onClick(nameko, type, value)}
        />
      </div>
    );
  }
}
